<template>
  <div class="loginput">
    <!--表单验证，验证通过后才能触发onLogin事件-->
    <van-form validate-first :show-error=false :show-error-message=false ref="forms" @submit="onLogin" @failed="onFailed">
      <!-- 输入手机号，调起手机号键盘 -->
      <van-field
        v-model="user.mobile"
        type="tel"
        icon-prefix="icon"
        left-icon="shouji"
        placeholder="请输入手机号码"
        name="mobile"
        :rules="formRules.mobile">
      </van-field>
      <van-field
        v-model="user.code"
        center
        clearable
        icon-prefix="icon"
        left-icon="yanzhengma"
        placeholder="请输入短信验证码"
        :rules="formRules.code">
        <div slot='extra' class="line"></div>
        <p slot='extra' class="getcode" align="center" v-if='bool' @click.prevent="onSendCode">获取验证码</p>
        <p slot='extra' class="getcode" align="center" v-else>{{ times }}s</p>
      </van-field>
      <div class='navbar-button'>
        <van-button
          type="info"
          native-type="submit"
          block
          color='#6db4fd'
          class='navbar-button'>
          登录/注册
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
// 调用封装登陆的请求模块
import { login, sendCode } from '@/api/user'

export default {
  name: 'logininput',
  components: {},
  props: {},
  data () {
    return {
      // 用户输入的数据(mobile和code是接口需要的数据字段)
      user: {
        mobile: '',
        code: ''
      },
      color: '#6db4fd',
      // 表单效验规则
      formRules: {
        // 手机号效验
        mobile: [
          // 校验手机号是否为空
          { required: true, message: '手机号码不能为空' },
          // 校验手机号是否符合手机规范
          { pattern: /^1(3|4|5|7|8|9)\d{9}$/, trigger: 'onBlur', message: '手机号码格式不正确' }
        ],
        // 验证码校验
        code: [
          { required: true, message: '验证码不能为空' },
          { pattern: /^\d{6}$/, message: '验证码不符合规范' }
        ]
      },
      bool: true,
      times: 60
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    // async 申明该方法中有异步操作
    async onLogin () {
      this.$toast.loading({
        message: '登录中...',
        // 禁止背景点击
        forbidClick: true,
        // 不会自己消失（网络请求完成后才消失）
        duration: 0
      })
      // 1找到数据接口
      // 2封装请求方法
      // 3请求调用登陆(并且把登陆的请求体以对象形式传递进去)
      // login(
      //   this.user
      // ).then(res => {
      //   this.$router.push('/')
      //   this.$toast.success('登陆成功')
      // }).catch(err => {
      //   console.log(err)
      //   this.$toast.fail('手机号或验证码错误')
      // })

      // try捕获异常
      try {
        // await阻塞 - 等待请求发送完毕拿到响应
        const response = await login(this.user)

        // 调用vuex中删除缓存组件的方法
        this.$store.commit('removeCould', 'TabbarIndex')

        // this.$router.back() // 返回登录前的页面（不推荐）
        this.$router.push(this.$route.query.r || '/') // 获取到query中存放的登录前的页面路径来进行跳转，实现登录后返回之前页面 (没有就默认跳转到首页)

        this.$toast.success('登陆成功')
        console.log(response)
        // 用户登陆的token提交并存储到vuex中
        this.$store.commit('setToken', response.data.data)
        // 抛出异常
      } catch (err) {
        console.log(err)
        this.$toast.fail('手机号或验证码错误')
      }
    },
    // 校验不通过时调用的方法(接受错误信息，错误信息包含提示)
    onFailed (err) {
      // 验证不通过时就弹出tost弹出框提示
      this.$toast({
        message: err.errors[0].message,
        position: 'top'
      })
    },
    // 发送验证码的方法
    async onSendCode () {
      /*
      * 校验手机号
      * 验证通过 > 发送请求验证码 > 用户接收短信 > 输入验证码 > 请求登陆
      * 发送请求验证码 > 隐藏发送按钮 > 显示倒计时按钮 > 倒计时结束，隐藏倒计时按钮 > 显示发送按钮
      */
      try {
        // 验证手机号(先找到form表单，再通过name属性找到表单中的校验项)
        await this.$refs.forms.validate('mobile')

        // TODO 验证码请求  （把输入的手机号参数传递进去）
        await sendCode(this.user.mobile)
        // 用于隐藏发送验证码按钮(手机号通过表单验证后倒计时)
        this.bool = !this.bool
        const timeouts = setInterval(() => {
          this.times = this.times - 1
          if (this.times <= 1) {
            clearInterval(timeouts)
            this.times = 60
            this.bool = !this.bool
          }
        }, 1000)
        this.$toast({
          message: '验证码发送成功',
          position: 'top'
        })
        // 这里捕获手机合法性异常和验证发送频繁异常
      } catch (err) {
        // 用于存放异常提示文案
        let message = String
        // // 判断异常类型
        // if (err && err.response && err.response.status === 429) {
        //   message = '验证码发送频繁，请稍后重试'
        // } else if (err.name === 'mobile') {
        //   message = err.message
        // } else {
        //   message = '发送失败，稍后再试'
        // }
        // // 验证不通过时就弹出tost弹出框提示
        // this.$toast({
        //   message,
        //   position: 'top'
        // })
        // 验证不通过时就弹出tost弹出框提示
        if (err && err.response && err.response.status === 429) {
          message = '验证码发送频繁，请稍后重试'
        } else if (err.name === 'mobile') {
          message = err.message
        } else {
          message = '发送失败'
        }
        this.$toast({
          message,
          position: 'top'
        })
      }
    }
  }
}
</script>

